import {search} from '../../api/http.js'
export default {
	data() {
		return {
			page:1,
			keywords:"",
			showMore:false,
			more:"加载更多...",
			searchItem:[]
		}
	},
	onLoad(){
		this.getSearchList()
	},
	onPullDownRefresh() {
		this.getSearchList()
		this.showMore = false
	},
	onReachBottom() {
		var page = ++this.page
		if(this.more == '我是有底线的'){
			return
		}
			
		search(this.keywords,page,15).then(res=>{
				this.showMore = true
			if(res.data.rows.length == 0){
				this.more = '我是有底线的'
			}else{
				this.more = '加载更多...'
				this.searchItem = this.searchItem.concat(res.data.rows)
			}
		})
		
	},
	methods: {
		random (lower, upper) {
			return Math.floor(Math.random() * (upper - lower)) + lower;
		},
		//随机获取列表1-6页内容
		getSearchList(){
			uni.showLoading({
				mask: true,
				title: "请稍后..."
			});
			let keywords = ""
			let num = this.random(1,6)
			let pageSize = ""
			search(keywords,num,pageSize).then(res=>{
				this.searchItem = res.data.rows
			})				
		},
		sure(e){
			this.page = 1
			this.more = ''
			this.keywords = e.detail.value;
			search(this.keywords,1,15).then(res=>{
				if(res.data.rows.length == 0){
					this.searchItem = []
					this.showMore = true
					this.more = '暂无数据'
				}else{
					this.searchItem = res.data.rows
				}
			})
		},
		
		goDetails(id){
			uni.navigateTo({
				url:`../details/details?id=${id}`
			})
		}
		
	}
}
